@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
    box-sizing: border-box;
}

/* Definição da cor de fundo da página, da cor e da fonta das letras e também da margem */
body {
    background-color: #111;
    color: #fff;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

/* Definição da margem entre as listas */
.listas {
    margin-top: -170px;
}

/* Definição da margem e do alinhamento do texto na parte inferior da página, no rodapé (footer) */
footer {
    margin: 50px 0;
    text-align: center;
}

/* Definição do GIF (Graphics Interchange Format) ou intercâmbio de gráficos, ou seja, imagem em movimento, no abrir da página */
.carregando {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 99;
    background-color: #000;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
/* CSS separado para cada um dos itens que foram utilizados para melhorar a organização */
LinhasDeFilme {
    margin-bottom: 30px;

}
.linhasDeFilme h2 {
    margin: 0px 0px 0px 30px;
}
.linhasDeFilme--areaDaLista {
    overflow-x: hidden;
    padding-left: 30px;
}
.linhasDeFilme--lista {
    transition: all ease 0.5s;
}
.linhasDeFilme--item {
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

/* Efeito: ao passar o mouse sobre a imagem/capa da filme ou serie ela amplia  */
.linhasDeFilme--item img {
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0,3s;
}
.linhasDeFilme--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}

/* Funcionalidade de mover as listas de filmes para a esquerda e para a direita */
.linhaDeFilme--direita,
.linhaDeFilme--esquerda {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}
/* Função para as setas de ir para esquerda e para direita, ficarem nos seus devidos lugares */
.linhaDeFilme--esquerda {
    left: 0;
}

.linhaDeFilme--direita {
    right: 0;
}

/* Efeito para aparecer as setas de mover as listas para a esquerda e para direita quando se passa o mouse em cima de cada lista */
.linhasDeFilme:hover .linhaDeFilme--esquerda,
.linhasDeFilme:hover .linhaDeFilme--direita {
    opacity: 1;
}

/* Seção responsável pela responsividade, como em dispositivos touch não existem a presença de um cursor, as setas para a 
esquerda e direita, precisam estar constantemente aparecendo */
@media (max-width: 760px) {
    .linhaDeFilme--direita,
    .linhaDeFilme--esquerda {
        opacity: 1;
    }
}
/* Definição da altura do filme destaque */
.destaque {
    height: 100vh;
}

/* Definição do efeito esmaecer (fade) na vertical do destaque */
.destaque--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

/* Definição do efeito esmaecer (fade) na horizontal do destaque */
.destaque--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 20%, transparent 80%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;

}

/* Definição do tamanho e da fonte da letra no nome do filme destaque */
.destaque--nome {
    font-size: 60px;
    font-weight: bold;
}

/* Definição do tamanho e da fonte da letra das informações do filme destaque */
.destaque--info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

/* Definição da disposição da avaliação, do ano de lançamento e da quantidade de temporadas do filme destaque */
.destaque--pontos, 
.destaque--ano, 
.destaque--temp {
    display: inline-block;
    margin-right: 15px;
}

/* Definição da cor da avaliação do filme destaque */
.destaque--pontos {
    color: #46d369;
}

/* Definição da cor, da fonte e da margem da sinopse do filme destaque */
.destaque--sinopse {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 30%;
}

/* Definição da margem dos botões no filme destaque */
.destaque--botoes {
    margin-top: 15px;
}

/* Definição das características dos botões "Assistir" e "+ Minha Lista" filme destaque */
.destaque--botaoassistir,
.destaque--botaolista {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

/* Definição do efeito ao passar o mouse nos botões "Assistir" e "+ Minha Lista" do filme destaque */
.destaque--botaoassistir:hover,
.destaque--botaolista:hover {
    opacity: 0.7;
}

/* Definição das cores do botão "Assistir" */
.destaque--botaoassistir {
    background-color: #fff;
    color: #000;
}

/* Definição das cores do botão "+ Minha Lista" */
.destaque--botaolista {
    background-color: #333;
    color: #fff;
}

/* Definição das características das informações dos gêneros */
.destaque--generos {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

/* Parte responsável pela responsividade do projeto e da página */
/* Algumas mudanças para a página ficar mais agradável para o usuário que está em outro dispositivo fora o computador */
@media (max-width:760px) {
    .destaque {
        height: 90vh;
    }
    .destaque--nome {
        font-size: 40px;
    }
    .destaque--info {
        font-size: 16px;
    }
    .destaque--sinopse {
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .destaque--botaoassistir,
    .destaque--botaolista {
        font-size: 16px;
    }
    .destaque--generos {
        font-size: 14px;
    }
}
header {
/* Definição do cabeçalho */
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.3s;
}
/* Definição do fundo do cabeçalho */
header.fundo {
    background-color: #141414;
}
/* Definição do espaço disponível para a logo do cabeçalho */
.cabecalho--logo {
    height:  50px;
}
/* Definição de quanto a imagem do logo do cabeçalho vai usar do espaço disponível */
.cabecalho--logo img {
    height: 100%;
}
/* Definição do espaço disponível para a logo do usuário no cabeçalho */
.cabecalho--usuario {
    height: 40px;
}
/* Definição de quanto a imagem do usuário do cabeçalho vai usar do espaço disponível */
.cabecalho--usuario img {
    height: 100%;
    border-radius: 3px;
}
